<template>
  <v-container class="pa-0">
    <v-layout column>
      <h1 class="mb-3 title text-xs-center">Orders</h1>
    </v-layout>

    <v-list three-line>
      <template v-for="(order, index) in orders">
        <v-divider/>
        <order-list-item v-if="order.status >= 2" :order="order"/>
        <order-list-item-title v-else
          style="padding-right: 72px !important"
          :order="order"
        />
      </template>
    </v-list>
  </v-container>
</template>

<script>
import OrderListItem from '@/components/orders/OrderListItem.vue';
import OrderListItemTitle from '@/components/orders/OrderListItemTitle.vue';

export default {
  components: {
    'order-list-item': OrderListItem,
    'order-list-item-title': OrderListItemTitle,
  },
  computed: {
    orders() {
      let orders = this.$store.state.api.orders;

      let separatedOrders = this.separateOrdersByStall(orders);
      for (let uid in separatedOrders) {
        separatedOrders[uid] = this.consolidateOrders(separatedOrders[uid]);
      }

      let combinedOrders = this.combineOrdersByStall(separatedOrders);
      this.sortOrdersByOrderTime(combinedOrders);
      return combinedOrders;
    },
  },
  methods: {
    separateOrdersByStall(orders) {
      let stallUids = new Set(orders.map(order => order.stall_uid));

      let separatedOrders = {};
      for (let uid of stallUids)
        separatedOrders[uid] = orders.filter(order => order.stall_uid === uid);

      return separatedOrders;
    },

    consolidateOrders(orders) {
      let receiptIds = new Set(orders.map(order => order.receipt_id));
      let consolidatedOrders = [];

      // all orders at this stage are from same stall
      let stallUid = orders[0].stall_uid;

      for (let id of receiptIds) {
        let items = orders.filter(order => order.receipt_id === id);

        let totalPrice = 0;
        for (let item of items) totalPrice += item.total_price;

        let orderTime = items[0].start_datetime;
        let deliveryTime = items[0].delivery_time;
        let status = Math.min(...items.map(item => item.status_id));

        consolidatedOrders.push({
          id,
          stallUid,
          totalPrice,
          orderTime,
          deliveryTime,
          status,
          items,
        });
      }

      return consolidatedOrders;
    },

    combineOrdersByStall(orders) {
      let combinedOrders = [];
      for (let uid in orders)
        combinedOrders.push(...orders[uid]);
      return combinedOrders;
    },

    sortOrdersByOrderTime(orders) {
      function compare(order1, order2) {
        let date1 = new Date(order1.orderTime);
        let date2 = new Date(order2.orderTime);
        return date2 - date1;
      }
      orders.sort(compare);
    },
  },
};
</script>
