<template>
  <v-btn icon class="ma-0 ml-1" @click="$emit('toggle')">
    <v-icon medium :class="active ? 'active' : 'inactive'">
      {{ icon }}
    </v-icon>
  </v-btn>
</template>

<script>
export default {
  props: ['active'],
  computed: {
    icon() { return this.active ? 'star' : 'star_border' },
  },
};
</script>

