<template>
  <div id="compulsory-options">
    <option-set v-for="(optionSet, index) in compulsoryOptions"
      :key="index"
      :item="optionSet" />
  </div>
</template>

<script>
import CompulsoryOptionSet from './CompulsoryOptionSet.vue';

export default {
  components: {
    'option-set': CompulsoryOptionSet,
  },

  computed: {
    item() { return this.$store.state.orders.currentItem },
    compulsoryOptions() {
      if (this.item === null) return [];
      else return this.item.compulsory_options;
    }
  },
};
</script>
