<template>
  <v-carousel light
    :cycle="false"
    hide-controls
    class="my-4 elevation-0"
    :height="500"
    v-model="carousel"
  >
    <v-carousel-item v-for="(item, i) in stalls"
      :key="i"
      :value="item.uid"
    >
      <RestaurantCard :item="item"/>
    </v-carousel-item>
  </v-carousel>
</template>

<script>
import { mapState } from 'vuex';
import RestaurantCard from './restaurantcard/RestaurantCard.vue';

export default {
  components: {
    RestaurantCard,
  },

  computed: {
    ...mapState({
      stalls: state => state.api.stalls,
    }),

    carousel: {
      get() { return this.$store.state.activeStallUid },
      set(uid) { this.$store.commit('setActiveStall', uid) },
    },
  },
};
</script>

<style>
.v-carousel__controls {
  background: none !important;
  opacity: 0.5;
  z-index: 0;
}
</style>
