<template>
  <v-card flat class="px-3">
    <v-layout row>
      <v-flex>
        <v-card-title class="pa-0">
          <span class="font-weight-bold fs-20">{{ formatted.title }}</span>
        </v-card-title>

        <v-card-text
          class="pa-0"
          style="opacity: 0.8"
        >
          <ul class="text-capitalize fs-16">
            <li v-for="options in item.compulsory_options">
              {{ options.name }}:
              {{ options.options[0].name }}
            </li>
            <li v-for="options in item.optional_options"
              v-if="options.options.length > 0"
            >
              {{ options.name }}:
              {{ options.options.map(obj => obj.name).join(', ') }}
            </li>
          </ul>
        </v-card-text>
      </v-flex>

      <v-flex xs4 align-center text-xs-right d-flex>
        <span class="pa-1 fs-20 font-weight-bold">
          {{ formatted.price }}
        </span>
        <v-btn icon class="ma-0"
          @click="$emit('remove')"
        >
          <v-icon>delete</v-icon>
        </v-btn>
      </v-flex>
    </v-layout>
  </v-card>
</template>

<script>
export default {
  props: ['item'],
  computed: {
    formatted() {
      let { name, quantity } = this.item;
      let price = this.item.unitCost;
      let total = price * quantity;

      return {
        title: `(${quantity}x) ${name}`,
        price: `$${parseFloat(total).toFixed(2)}`,
      };
    },
  },
};
</script>
