const cloneDeep = require('lodash/cloneDeep');

const state = {
  currentItem: null,
  dialog: false,
};

const getters = {
  parseCurrentItem: (state, getters) => {
    let item = cloneDeep(state.currentItem);
    if (item === null) return null;
    // no map is used for cleaning of options because of scoping
    // i.e. objects created inside scope of map become undefined after

    for (let optionSet of item.compulsory_options) {
      let selection = optionSet.selected;
      optionSet.options = [optionSet.options[selection]];
    }

    for (let optionSet of item.optional_options) {
      optionSet.options = optionSet.options.filter(option => option.selected);
    }

    return item;
  },

  currentItemCost: (state, getters) => {
    let item = getters.parseCurrentItem;
    if (item === null) return 0;

    function sum(arr) {
      let add = (x, y) => (x + y);
      return arr.reduce(add, 0);
    }
    function sumOptions(options) {
      let total = 0;
      for (let optionSet of options) {
        let costs = optionSet.options.map(option => option.cost);
        total += sum(costs);
      }
      return total;
    }

    let unitCost = parseFloat(item.base_price)
                   + sumOptions(item.compulsory_options)
                   + sumOptions(item.optional_options);
    let quantity = item.quantity;
    return unitCost * quantity;
  },

  validateCurrentItem: (state, getters) => {
    let item = getters.parseCurrentItem;
    if (item === null) return false;

    function all(arr) {
      let and = (x, y) => (x && y);
      return arr.reduce(and, true);
    }

    let checkCompulsory = all(item.compulsory_options.map(
      optionSet => optionSet.options.length === 1
    ));
    return checkCompulsory;
  },
};

const mutations = {
  openDialog(state) { state.dialog = true },
  closeDialog(state) { state.dialog = false },
};

const actions = {
  selectItem({ state, rootState }, item) {
    let newItem = cloneDeep(item);
    newItem['quantity'] = 1;
    newItem['message'] = {
      selected: false,
      text: '',
    };

    // select first item of every compulsory option by default
    for (let key in newItem.compulsory_options)
      newItem.compulsory_options[key].selected = 0;

    // TODO - forced myx stall to have 50% sugar
    if (rootState.activeStallUid === 'myx')
      for (let i in newItem.compulsory_options) {
        let { name } = newItem.compulsory_options[i];
        if (name === 'Sugar') newItem.compulsory_options[i].selected = 2;
      }

    state.currentItem = newItem;
  },
};

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions,
};
