<template>
  <v-dialog fullscreen persistent
    v-model="paymentOpen"
    content-class="background-cover"
  >
    <order-processing v-if="stage === stages.ORDER_PROCESSING"/>
    <awaiting-payment v-if="stage === stages.AWAITING_PAYMENT"
      @pay="pay"
    />
    <payment-processing v-if="stage === stages.PAYMENT_PROCESSING"/>
    <payment-done v-if="stage === stages.PAYMENT_DONE"/>
    <payment-rejected v-if="stage === stages.PAYMENT_REJECTED"/>
  </v-dialog>
</template>

<script>
import { mapState } from 'vuex';
import OrderProcessing from './OrderProcessing.vue';
import AwaitingPayment from './AwaitingPayment.vue';
import PaymentProcessing from './PaymentProcessing.vue';
import PaymentDone from './PaymentDone.vue';
import PaymentRejected from './PaymentRejected.vue';

export default {
  components: {
    'order-processing': OrderProcessing,
    'awaiting-payment': AwaitingPayment,
    'payment-processing': PaymentProcessing,
    'payment-done': PaymentDone,
    'payment-rejected': PaymentRejected,
  },

  computed: {
    ...mapState(['paymentOpen']),
    ...mapState('payment', ['stages', 'stage']),
  },

  methods: {
    pay() {
      this.$store.commit('payment/step');
      this.awaitPaymentProcessing();
    },
    awaitPaymentProcessing() {
      setTimeout(() => { this.$store.commit('payment/step') }, 1500);
      setTimeout(() => { this.$store.commit('closePayment') }, 1500 + 1500);
    },
  },
};
</script>

<style lang="scss">
@import '@/assets/css/main.scss';

.background-cover {
  background: rgba(black, 0.85);
  color: white;
}

.progress-circular {
  color: $primary-color;
}
</style>
