<template>
  <div>
    <v-checkbox hide-details class="ma-0 pl-1"
      v-model="selected"
      label="Custom message to seller" />
    <v-expand-transition>
      <v-textarea hide-details outline class="mt-3"
        v-if="selected"
        v-model="message"
        label="Message to seller..."
      />
    </v-expand-transition>
  </div>
</template>

<script>
export default {
  computed: {
    item() { return this.$store.state.orders.currentItem },
    selected: {
      get() {
        if (this.item === null) return false;
        else return this.item.message.selected;
      },
      set(value) {
        if (this.item !== null) this.item.message.selected = value;
      },
    },
    message: {
      get() {
        if (this.item === null) return '';
        else return this.item.message.text;
      },
      set(value) {
        if (this.item !== null) {
          this.item.message.text = value;
          this.selected = Boolean(this.message);
        };
      },
    },
  },
};
</script>
