<template>
  <v-list-tile
    :disabled="!item.in_menu || !item.in_stock"
    class="px-2"
    :class="{ selected: currQuantity > 0 }"
    @click="openDialog"
  >
    <v-list-tile-content class="fs-16">
      <v-list-tile-title>{{ item.name }}</v-list-tile-title>
      <v-list-tile-sub-title v-if="item.description" class="subtitle-noah">
        {{ item.description }}
      </v-list-tile-sub-title>
    </v-list-tile-content>

    <v-list-tile-avatar class="fs-16">
      <span v-if="currQuantity === 0">
        {{ formatted.price }}
      </span>
      <v-btn icon disabled
        v-if="currQuantity !== 0"
        class="primary fs-16 font-weight-bold"
      >
        {{ currQuantity }}
      </v-btn>
    </v-list-tile-avatar>
  </v-list-tile>
</template>

<script>
export default {
  props: ['item'],

  computed: {
    formatted() {
      return {
        price: `$${ parseFloat(this.item.base_price).toFixed(2) }`,
      };
    },
    currQuantity() {
      let id = this.item.id;
      return this.$store.getters['cart/totalItemsById'](id);
    },
  },

  methods: {
    openDialog() {
      this.$store.dispatch('orders/selectItem', this.item);
      this.$store.commit('orders/openDialog');
    },
  },
};
</script>

<style lang="scss" scoped>
.selected {
  background-color: #fffce1;
}
</style>
