<template>
  <div id="header">
    <v-layout>
      <v-icon :size="28" class="ma-0 mr-2">location_on</v-icon>
      <span class="title title-ibm-mono">{{ formatted.title }}</span>
      <v-icon :size="28" class="ma-0 ml-2">a</v-icon>
    </v-layout>
    <span class="subheading">{{ formatted.description }}</span>
  </div>
</template>

<script>
export default {
  props: ['title', 'description'],

  computed: {
    formatted() {
      let { title, description } = this;
      return {
        title: title.toUpperCase(),
        description: description.toUpperCase(),
      };
    },
  },
};
</script>

