<template>
  <v-menu
    v-model="menu"
    content-class="menu elevation-1"
  >
    <template v-slot:activator="{ on }">
      <v-btn icon v-on="on">
        <v-icon>menu</v-icon>
      </v-btn>
    </template>

    <v-btn icon
      style="top: 8px; margin: 6px;"
      @click="menu = false"
    >
      <v-icon :size="30">menu</v-icon>
    </v-btn>

    <v-layout row justify-space-around
      class="mx-3 mb-3"
    >
      <menu-button
        v-if="isOrdersPage"
        icon="restaurant_menu"
        text="Shops"
        @click="$router.push('/main')"
      />
      <menu-button
        v-else
        icon="receipt"
        text="Orders"
        @click="$router.push('orders')"
      />
      <menu-button
        icon="notifications"
        text="Notific."
      />
      <menu-button
        icon="exit_to_app"
        text="Logout"
        @click="logout"
      />
    </v-layout>
  </v-menu>
</template>

<script>
import MenuButton from './MenuButton.vue';

export default {
  components: {
    'menu-button': MenuButton,
  },

  computed: {
    menu: {
      get() { return this.$store.state.toolbarOpen },
      set(value) { this.$store.state.toolbarOpen = value },
    },
    isOrdersPage() {
      return this.$route.path === '/orders';
    },
  },

  methods: {
    logout() {
      let auth2 = gapi.auth2.getAuthInstance();
      auth2.signOut()
      .then(() => {
        this.$store.commit('logout');
        this.$router.push('/');
      });
    },
  },
};
</script>

<style scoped>
.menu {
  background-color: white;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;

  top: 0 !important;
  left: 0 !important;

  max-width: 100%;
  width: 100%;
}
</style>
