<template>
  <v-card flat
    class="overflow-hidden"
    @click="$emit('click')"
  >
    <v-card-title class="py-1">
      <v-icon medium left class="mr-2">location_on</v-icon>
      <span class="title title-ibm-mono">{{ title.toUpperCase() }}</span>
      <v-icon medium>expand_more</v-icon>
    </v-card-title>

    <v-card-text class="pt-1 pb-2 text-uppercase fs-16">
      {{ heading }}
    </v-card-text>

    <v-layout row class="mx-3 overflow-x-scroll">
      <img v-for="item in stalls"
        class="mr-1"
        height="128"
        :src="imageUrl(item.image_url)"
      />
    </v-layout>
  </v-card>
</template>

<script>
import { mapState } from 'vuex';

export default {
  data() {
    return {
      title: 'SUTD',
      heading: 'Nearby Restaurants',
    };
  },
  computed: mapState({
    stalls: state => state.api.stalls,
  }),
  methods: {
    imageUrl(path) {
      return this.$store.getters['api/imageUrl'](path);
    },
  },
};
</script>

<style lang="scss" scoped>
@import '@/assets/css/main.scss';

.v-icon {
  color: $primary-color;
}

.overflow-hidden {
  overflow: hidden;
}

.overflow-x-scroll {
  overflow-x: scroll;
  scrollbar-width: none;
}

::-webkit-scrollbar {
  width: 0px;
  background: transparent;
}
</style>