<template>
  <div class="mb-3">
    <p id="header">{{ item.name }}</p>
    <v-checkbox v-for="option in item.options" :key="option.name"
      :disabled="isMyx"
      color="#707070"
      v-model="option.selected"
      class="text-capitalize mt-0 pl-1"
      hide-details :ripple="false"
      :label="formatLabel(option)"
    />
  </div>
</template>

<script>
export default {
  props: ['item'],
  methods: {
    formatLabel(option) {
      let { name, cost } = option;
      cost = cost.toFixed(2);
      return `${name} (+$${cost})`;
    },
  },
  computed: {
    isMyx() {
      return this.$store.state.activeStallUid === 'myx';
    },
  },
};
</script>

<style scoped>
#header {
  margin: 0px;
  font-size: 18px;
}
</style>
