<template>
  <v-app>
    <toolbar/>

    <v-content
      :style="{ opacity }"
    >
      <router-view/>
    </v-content>

    <v-snackbar
      v-model="snackbar"
      top
      :timeout="3000"
      class="fs-16"
    >
      Order {{ orderId }} is ready to be collected!
      <v-btn flat color="#fcc615" @click="snackbar = false">
        Close
      </v-btn>
    </v-snackbar>
  </v-app>
</template>

<script>
import Toolbar from '@/components/toolbar/MainToolbar.vue';

export default {
  name: 'App',
  components: {
    'toolbar': Toolbar,
  },

  computed: {
    opacity() {
      if (this.$store.state.toolbarOpen) return 0.5;
      else return 1.0;
    },
    snackbar: {
      get() { return this.$store.state.snackbar },
      set(value) { this.$store.state.snackbar = value },
    },
    orderId() { return this.$store.state.payment.activeReceiptId },
  },
};
</script>

