<template>
  <v-dialog scrollable v-model="dialog" content-class="top-border bottom-border">
    <v-card class="top-border bottom-border">
      <v-card-title primary-title
        class="text-uppercase font-weight-bold primary top-border"
        style="justify-content: center"
      >
        <span class="underline subtitle-ibm-sans">Shopping Cart</span>
      </v-card-title>

      <v-card-text class="pa-0">
        <cart-list :cart="cart"/>
      </v-card-text>

      <v-card-actions class="pa-0">
        <v-btn large block flat
          class="primary bottom-border subtitle-ibm-sans"
          @click="pay"
        >
          Confirm - {{ price }}
        </v-btn>
      </v-card-actions>
    </v-card>

    <payment-dialog/>
  </v-dialog>
</template>

<script>
import CartList from './CartList.vue';
import PaymentDialog from '@/components/nearby/payment/PaymentDialog.vue';

export default {
  components: {
    'cart-list': CartList,
    'payment-dialog': PaymentDialog,
  },
  computed: {
    dialog: {
      get() { return this.$store.state.cartOpen },
      set(value) { this.$store.state.cartOpen = value },
    },
    cart() {
      return this.$store.getters['cart/activeCart'];
    },
    price() {
      let price = this.$store.getters['cart/totalPrice'];
      return `$${price.toFixed(2)}`;
    },
  },
  methods: {
    pay() {
      if (this.cart.length === 0) return;
      this.$store.dispatch('openPayment');
      this.$store.dispatch('api/sendOrders');
    },
  },
}
</script>

<style lang="scss">
$border-radius: 16px;

.top-border {
  border-top-left-radius: $border-radius;
  border-top-right-radius: $border-radius;
}

.bottom-border {
  border-bottom-left-radius: $border-radius;
  border-bottom-right-radius: $border-radius;
}

span.underline {
  padding: 0px 4px;
  border-bottom-style: solid;
  border-bottom-width: 2px;
  border-bottom-color: rgba(255, 255, 255, 0.2);
}
</style>
