import io from 'socket.io-client';
const host = 'myxbrewapi.com';

const state = {
  socket: io(`https://${host}`),
  stalls: [],
  menu: {},
  orders: [],
};

const getters = {
  imageUrl: (state) => (path) => {
    return `https://${host}/${path}`;
  },
};

const mutations = {
  setStalls(state, stalls) {
    state.stalls = stalls;
  },
  addMenu(state, { uid, menu }) {
    state.menu = {
      ...state.menu,
      [uid]: menu,
    };
  },
  setOrders(state, orders) {
    state.orders = orders;
  },
};

const actions = {
  fetchAll({ commit, dispatch, state, rootState }) {
    dispatch('getStalls')
    .then(stalls => {
      for (let stall of stalls)
        dispatch('getMenu', stall.uid);

      commit('setActiveStall', stalls[0].uid, { root: true });
    });
    dispatch('getOrders');

    state.socket.emit('customer_join', rootState.customer.id);
    state.socket.on('orders', orders => {
      dispatch('payment/checkOrderStatus', orders, { root: true });
    });
  },

  sendOrders({ commit, dispatch }) {
    dispatch('postOrders')
    .then(receiptId => {
      commit('cart/clearCart', null, { root: true });
      dispatch('payment/registerActiveOrders', receiptId, { root: true });
    });
  },

  getStalls({ commit }) {
    let lat = '1d341126';
    let long = '103d963564';
    let url = `https://${host}/stalls/${lat}/${long}`;

    return fetch(url)
    .then(res => res.json())
    .then(stalls => {
      commit('setStalls', stalls);
      return stalls;
    });
  },
  getMenu({ commit }, uid) {
    let url = `https://${host}/menu/${uid}`;

    return fetch(url)
    .then(res => res.json())
    .then(menu => {
      commit('addMenu', { uid, menu });
      return menu;
    });
  },
  getOrders({ commit, state, rootState }) {
    let customerId = rootState.customer.id;
    let url = `https://${host}/orders/customer/${customerId}`;

    return fetch(url)
    .then(res => res.json())
    .then(orders => {
      commit('setOrders', orders);
      return orders;
    });
  },

  postOrders({ state, rootState, rootGetters }) {
    let uid = rootState.activeStallUid;
    let stall = state.stalls.filter(stall => stall.uid === uid)[0];

    let metadata = {
      client_type:  rootState.customer.type,
      customer_id: rootState.customer.id,
      total_payment: rootGetters['cart/totalPrice'],
      uid: stall.uid,
      menu_version: stall.latest_menu_version,
      delivery_time: new Date().toISOString(),
    };

    function spreadOrders(orders) {
      let result = [];
      for (let order of orders) {
        for (let i=0; i<order.quantity; i+=1)
          result.push({
            ...order,
            item_id: order.id,
            total_price: order.unitCost,
            note: order.message.selected ? order.message.text : '',
          });
      }
      return result;
    }
    let orders = spreadOrders(rootState.cart.cart[uid]);

    let url = `https://${host}/order`;
    return fetch(url, {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({ metadata, orders }),
    })
    .then(res => res.json())
    .then(json => json.receipt_id)
    .catch(console.err);
  },

  postCustomer({}, { id, email }) {
    let url = `https://${host}/customer`;
    return fetch(url, {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({ id, email }),
    })
    .then(res => {
      if (res.ok) return res.json();
      else throw `POST customer failed: (${id}, ${email})`;
    });
  }
};

export default {
  namespaced: true,
  getters,
  state,
  mutations,
  actions,
};
