import Vue from 'vue';

const stages = {
  ORDER_PROCESSING: 0,
  AWAITING_PAYMENT: 1,
  PAYMENT_PROCESSING: 2,
  PAYMENT_DONE: 3,
  PAYMENT_REJECTED: -1,
};

const state = {
  stages,
  stage: stages.ORDER_PROCESSING,
  activeReceiptId: null,
};

const getters = {};

const mutations = {
  step(state) {
    state.stage += 1;
  },
  initPayment(state) {
    state.stage = stages.ORDER_PROCESSING;
  },

  setActiveReceipt(state, receiptId) {
    state.activeReceiptId = receiptId;
  },
};

const actions = {
  registerActiveOrders({ dispatch, commit, state, rootState }, receiptId) {
    commit('setActiveReceipt', receiptId);
  },

  checkOrderStatus({ commit, dispatch, state }, orders) {
    let activeId = state.activeReceiptId;
    let activeOrders = orders.filter(order => order.receipt_id === activeId);
    let orderStatus = Math.min(...activeOrders.map(order => order.status_id));

    if (orderStatus == 1) state.stage = stages.AWAITING_PAYMENT;
    else if (orderStatus < 0) {
      state.stage = stages.PAYMENT_REJECTED;
      setTimeout(() => { commit('closePayment', null, { root: true }) }, 2500);
    }
    else if (orderStatus === 4) {
      dispatch('notification', null, { root: true });
    }
  },
};

export default {
  namespaced: true,
  getters,
  state,
  mutations,
  actions
};
