<template>
  <v-card flat>
    <v-card-text class="pb-0 fs-16">
      <ul>
        <li v-for="item in order.items">
          <v-layout row>
            <span>
              {{ item.name }}
            </span>
            
            <v-spacer/>
            
            <span class="px-3 font-weight-bold">
              ${{ item.total_price.toFixed(2) }}
            </span>
          </v-layout>
        </li>
      </ul>

      <v-layout row class="pt-2">
        <h1 class="caption pt-1">
          Ordered on {{ orderTime.date }} at {{ orderTime.time }}
        </h1>
        <v-spacer/>
        <span class="px-3 font-weight-bold">
          Total: ${{ order.totalPrice.toFixed(2) }}
        </span>
      </v-layout>

      <h1 class="pt-3 text-xs-center font-weight-bold fs-20">
        Order #{{ order.id }}
      </h1>
    </v-card-text>

    <v-container class="pt-0">
      <v-layout justify-center wrap>
        <v-flex xs6>
          <v-img :src="qrImage"/>
        </v-flex>
        <v-flex xs12 class="pa-0 text-xs-center">
          <span class="caption">
            Please scan QR code at collection station to collect your food.
          </span>
        </v-flex>
      </v-layout>
    </v-container>
  </v-card>
</template>

<script>
import moment from 'moment';
const QR = require('qrcode');

export default {
  props: ['order'],
  data() {
    return {
      qrImage: '',
    };
  },
  computed: {
    orderTime() {
      let datetime = moment(this.order.orderTime);
      let date = datetime.format('Do MMMM YYYY');
      let time = datetime.format('h:mma');
      return { date, time };
    },
  },

  methods: {
    createQR(text) {
      let options = {
        type: 'image/png',
        color: {
          dark: '#000',
          light: '#0000',
        },
      };
      return QR.toDataURL(text, options);
    },
  },

  mounted() {
    let text = `Myx Brew ${this.order.id.toString()}`;
    this.createQR(text)
    .then(url => { this.qrImage = url });
  }
};
</script>
