<template>
  <v-card flat class="restaurant-card text-xs-center" :height="450">
    <v-img class="mx-5"
      :src="imageUrl"
      :max-height="268"
    >
      <span
        id="distance"
        class="pt-1 px-2 font-weight-bold"
      >
        {{ formatted.distance }}
      </span>
    </v-img>

    <v-card-title class="pt-5 pb-0 justify-center">
      <!-- placeholder icon for alignment -->
      <v-icon class="ma-0">a</v-icon>
      <span id="title" class="title title-ibm-sans">{{ formatted.name }}</span>
      <favourite-icon :active="favourite" @toggle="handleFavourite" />
    </v-card-title>

    <v-card-text class="pt-0">
      <v-layout column>
        <span id="info" class="fs-20">
          {{ formatted.duration }} | 
          {{ formatted.halal }} | 
          {{ formatted.price }}
        </span>
        <span id="description" class="fs-16">{{ item.description }}</span>
        <span id="tags" class="mt-2 subheading">{{ formatted.tags }}</span>
      </v-layout>
    </v-card-text>
  </v-card>
</template>

<script>
import Vue from 'vue';
import FavouriteIcon from './FavouriteIcon.vue';

export default {
  props: ['item'],
  data() {
    return {
      favourite: false,
    };
  },
  components: {
    'favourite-icon': FavouriteIcon,
  },
  computed: {
    formatted() {
      let { distance, name, waiting_time, halal, price, tags } = this.item;

      return {
        distance: distance.toFixed(1) + 'km',
        name: name.toUpperCase(),
        duration: waiting_time + ' mins',
        halal: `${ halal ? 'Halal' : 'Non-Halal' }`,
        price: '$ '.repeat(price),
        tags: 'Tags: ' + tags.join(', '),
      };
    },
    imageUrl() {
      let path = this.item.image_url;
      return this.$store.getters['api/imageUrl'](path);
    },
  },
  methods: {
    handleFavourite() {
      this.favourite = !this.favourite;
    },
  },
};
</script>

