  padding: 0px 25px 0px 25px;
  overflow-y: scroll;
}

#distance {
  background-color: rgba(168, 168, 168, 0.64);
  color: #303030;
  font-size: 20px;

  position: absolute;
  bottom: 0;
  right: 0;
}

#title {
  color: #484848;
}

#info {
  color: #7070707f;
}

#description {
  color: #707070;
}

#tags {
  color: #7070704d;
}
</style>