<template>
  <div id="menu-container">
    <div id="menu">
      <v-btn flat block
        id="menu-btn"
        class="primary ma-0 slide-out"
        @click="slide"
      >
        <span class="title-ibm-sans fs-20">Menu</span>
      </v-btn>

      <menu-content :menu="menu" />

      <food-dialog />
      <cart-dialog />
    </div>
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex';
import MenuContent from './MenuContent.vue';
import FoodDialog from '@/components/nearby/fooddialog/FoodDialog.vue';
import CartDialog from '@/components/nearby/cartdialog/CartDialog.vue';

export default {
  components: {
    'menu-content': MenuContent,
    'food-dialog': FoodDialog,
    'cart-dialog': CartDialog,
  },
  computed: {
    ...mapState(['menuOpen']),
    ...mapGetters({ menu: 'activeMenu' }),
  },
  methods: {
    slide() {
      this.$store.commit('toggleMenu');
    },
  },
  watch: {
    menuOpen(val) {
      let $menu = document.getElementById('menu');
      $menu.className = val ? 'slide-in' : 'slide-out';
    },
  },
};
</script>

<style lang="scss" scoped>
@import '@/assets/css/main.scss';

$menu-height: calc(100% - #{$toolbar-height});
$menu-btn-height: 72px;
$menu-bottom: calc(#{$menu-btn-height} - #{$menu-height});
$menu-animation-top: calc(-100% + #{$menu-btn-height});
$slide-duration: 0.5s;

#menu-container {
  height: $menu-btn-height;
  width: 100%;
}

#menu {
  position: fixed;
  bottom: $menu-bottom;

  height: $menu-height;
  width: 100%;

  border-radius: 16px 16px 0px 0px;

  background-color: white;
}

#menu-btn {
  height: $menu-btn-height;
  border-radius: 16px 16px 0px 0px;
  font-size: 16px;
}

#menu-btn span {
  padding: 0px 4px;
  border-bottom-style: solid;
  border-bottom-width: 2px;
  border-bottom-color: rgba(255, 255, 255, 0.2);
}

#menu-content {
  display: block;
  height: calc(100% - #{$menu-btn-height});
  overflow-y: scroll;
}

.slide-in {
  animation-name: slide-in;
  animation-duration: $slide-duration;
  animation-fill-mode: forwards;
  animation-timing-function: ease-in;
}

.slide-out {
  animation-name: slide-out;
  animation-duration: $slide-duration;
  animation-fill-mode: forwards;
  animation-timing-function: ease-in;
}

@keyframes slide-in {
  0% { transform: translateY(0%) }
  100% { transform: translateY($menu-animation-top) }
}

@keyframes slide-out {
  0% { transform: translateY($menu-animation-top) }
  100% { transform: translateY(0%) }
}
</style>