<template>
  <v-list-tile
    class="full-width px-3"
    :class="{ 'reduced-opacity': completed }"
  >
    <v-layout row justify-space-between style="width: 100%">
      <v-layout column justify-center>
        <v-list-tile-title class="font-weight-bold">
          <v-icon
            :class="{ 'icon-enabled': !completed }"
          >location_on</v-icon>
          <span class="fs-20 font-weight-bold">{{ stallName }}</span>
        </v-list-tile-title>

        <v-list-tile-sub-title>
          <v-icon>a</v-icon>
          <span class="fs-16">{{ numItems }} items</span>
        </v-list-tile-sub-title>
      </v-layout>

      <div>
        <v-layout column justify-center fill-height>
          <p
            class="ma-0 text-xs-center outline"
            :class="{ ready, unpaid }"
          >
            {{ status }}
          </p>
          <p
            v-if="false"
            class="mt-1 mb-0 caption text-xs-right fs-16"
            :class="{ unpaid }"
          >
            Click here to pay
          </p>
          <p class="mt-1 mb-0 caption text-xs-right fs-16">
            {{ deliveryTime.time }}
          </p>
        </v-layout>
      </div>
    </v-layout>
  </v-list-tile>
</template>

<script>
let moment = require('moment');

export default {
  props: ['order'],
  computed: {
    stalls() { return this.$store.state.api.stalls },
    stallName() {
      let uid = this.order.stallUid;

      for (let stall of this.stalls)
        if (stall.uid === uid)
          return stall.name;

      return uid;
    },
    numItems() {
      return this.order.items.length;
    },
    deliveryTime() {
      let datetime = moment(this.order.deliveryTime);
      let date = datetime.format('Do MMMM YYYY');
      let time = datetime.format('h:mma');
      return { date, time };
    },

    status() {
      let statuses = {
        // 1. Pending acceptance
        // 2. Accepted but not acknowledged (as in, blinking until tapped)
        // 3. Awaiting processing
        // 4. Processed
        [-2]: 'Rejected',
        [-1]: 'Rejected',
        0: 'Unconfirmed',
        1: 'Unpaid',
        2: 'Preparing',
        3: 'Ready',
        4: 'Ready',
        5: 'Collected',
      };
      return statuses[this.order.status];
    },

    unpaid() {
      return this.order.status === 1; 
    },
    ready() {
      return this.order.status === 3 || this.order.status === 4;
    },
    completed() {
      return this.order.status === 5;
    },
  },
};
</script>

