  padding: 0px;
}

.v-icon.icon-enabled {
  color: $primary-color !important;
}

.reduced-opacity {
  opacity: 0.5;
}

p.outline {
  padding: 0px 8px;

  border: solid 1px black;
  border-radius: 1px;

  font-size: 12px;
  text-transform: uppercase;
}

p.ready {
  border-color: $primary-color;
  color: $primary-color;
}

p.unpaid {
  border-color: red;
  color: red;
}
</style>
