<template>
  <v-dialog v-model="dialog" content-class="food-dialog">
    <v-card id="card" class="px-3" height="500">
      <v-card-title style="height: 25%">
        <dialog-title />
        <dialog-quantity />
      </v-card-title>

      <v-card-text class="mb-3 pt-0 scrollable">
        <compulsory-options />
        <optional-options />
        <custom-message />
      </v-card-text>

      <v-card-actions class="py-0">
        <v-btn block depressed large
          style="border-radius: 12px"
          class="mx-2 primary title-ibm-sans fs-20"
          @click="closeDialog"
        >
          {{ cost }} Add to cart
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import DialogTitle from './DialogTitle.vue';
import DialogQuantity from './DialogQuantity.vue';
import CompulsoryOptions from './CompulsoryOptions.vue';
import OptionalOptions from './OptionalOptions.vue';
import CustomMessage from './CustomMessage.vue';

export default {
  components: {
    'dialog-title': DialogTitle,
    'dialog-quantity': DialogQuantity,
    'compulsory-options': CompulsoryOptions,
    'optional-options': OptionalOptions,
    'custom-message': CustomMessage,
  },
  computed: {
    dialog: {
      get() { return this.$store.state.orders.dialog },
      set(value) {
        // scroll dialog back to top, since it is shared by all items
        document.getElementsByClassName('scrollable')[0].scrollTop = 0;

        if (value) this.$store.commit('orders/openDialog');
        else this.$store.commit('orders/closeDialog');
      },
    },
    cost() {
      let cost = this.$store.getters['orders/currentItemCost'];
      return `$${ cost.toFixed(2) }`;
    },
  },
  methods: {
    closeDialog() {
      let item = this.$store.getters['orders/parseCurrentItem'];
      let valid = this.$store.getters['orders/validateCurrentItem'];

      if (valid) {
        this.$store.commit('cart/addItem', item);
        this.dialog = false;
      }
    },
  },
};
</script>

<style>
.food-dialog {
  overflow: hidden;

  position: absolute;
  bottom: 0px;
  margin: 0px;
  border-radius: 0px;
}
</style>

<style lang="scss" scoped>
@import '@/assets/css/main.scss';

.scrollable {
  overflow-y: scroll;
  height: calc(100% - 25% - 44px - 32px);
}

#card {
  border-radius: 0px;
}
</style>
