<template>
  <div class="mb-3">
    <p class="ma-0 fs-20">{{ item.name }}</p>
    <v-radio-group hide-details
      :disabled="isMyx"
      class="mt-0 pl-1 text-capitalize"
      v-model="item.selected"
    >
      <v-radio v-for="(option, index) in item.options" :key="index"
        color="#707070"
        :ripple="false"
        :label="formatLabel(option)"
        :value="index"
      />
    </v-radio-group>
  </div>
</template>

<script>
export default {
  props: ['item'],
  methods: {
    formatLabel(option) {
      let { name, cost } = option;
      cost = cost.toFixed(2);
      return `${name} (+$${cost})`;
    },
  },
  computed: {
    isMyx() {
      return this.$store.state.activeStallUid === 'myx';
    },
  },
};
</script>
