import Vue from 'vue';
import Vuex from 'vuex';
import orders from './modules/orders';
import cart from './modules/cart';
import api from './modules/api';
import payment from './modules/payment';

Vue.use(Vuex);

export default new Vuex.Store({
  modules: {
    orders,
    cart,
    api,
    payment,
  },

  state: {
    menuOpen: false,
    cartOpen: false,
    paymentOpen: false,
    toolbarOpen: false,

    activeStallUid: null,

    snackbar: false,

    customer: {
      id: -1,
      email: '',
      type: 'school',
    },
  },

  getters: {
    isLoggedIn(state) {
      return state.customer.id >= 0;
    },
    activeMenu(state) {
      let uid = state.activeStallUid;
      return state.api.menu[uid];
    },
  },

  mutations: {
    login(state, { id, email }) {
      state.customer = {
        ...state.customer,
        id,
        email,
      };
    },
    logout(state) {
      state.customer = {
        ...state.customer,
        id: -1,
        email: '',
      };
    },

    toggleMenu(state) {
      Vue.set(state, 'menuOpen', !state.menuOpen);
    },

    openPayment(state) {
      Vue.set(state, 'paymentOpen', true);
    },
    closePayment(state) {
      Vue.set(state, 'paymentOpen', false);
    },

    closeCart(state) {
      Vue.set(state, 'cartOpen', false);
    },

    setActiveStall(state, uid) {
      state.activeStallUid = uid;
      state.cart.activeStallUid = uid;
    },
  },

  actions: {
    openPayment({ commit }) {
      commit('toggleMenu');
      commit('closeCart');

      commit('payment/initPayment');
      commit('openPayment');
    },

    notification({ state }) {
      Vue.set(state, 'snackbar', true);
    },
  },
});
