<template>
  <v-list scrollable two-line class="pa-0">
    <v-list-tile class="ma-1" v-if="cart.length === 0">
      <v-list-tile-title class="fs-20">
        Cart is empty
      </v-list-tile-title>
    </v-list-tile>

    <template v-for="(item, index) in cart">
      <v-divider v-if="index > 0"/>
      <cart-item-card class="ma-1"
        :item="item"
        @remove="remove(index)"
      />
    </template>
  </v-list>
</template>

<script>
import CartItemCard from './CartItemCard.vue';

export default {
  props: ['cart'],
  components: {
    'cart-item-card': CartItemCard,
  },
  data() {
    return {
      dummy: {
        name: 'Nothing to see here',
      },
    };
  },
  methods: {
    remove(index) {
      this.$store.commit('cart/removeItem', index);
    },
  },
};
</script>
