<template>
	<div id="optional-options">
		<option-set v-for="(optionSet, index) in optionalOptions"
			:key="index"
			:item="optionSet" />
	</div>
</template>

<script>
import OptionalOptionSet from './OptionalOptionSet.vue';

export default {
	components: {
		'option-set': OptionalOptionSet,
	},

	computed: {
		item() { return this.$store.state.orders.currentItem },
		optionalOptions() {
			if (this.item === null) return [];
			return this.item.optional_options;
		},
	},
};
</script>
