<template>
  <div id="dialog-title" style="width: 60%">
    <p class="ma-0 fs-20 font-weight-bold max-lines-2">
      {{ name }}
    </p>
    <p class="ma-0 fs-20">{{ price }}</p>
    <p v-if="description" class="ma-0 subtitle-noah">
      {{ description }}
    </p>
  </div>
</template>

<script>
export default {
  computed: {
    item() { return this.$store.state.orders.currentItem },
    name() {
      if (this.item === null) return '';
      else return this.item.name;
    },
    price() {
      if (this.item === null) return '';
      else return '$' + parseFloat(this.item.base_price).toFixed(2);
    },
    description() {
      if (this.item === null) return '';
      else return this.item.description;
    }
  },
};
</script>

<style scoped>
.max-lines-2 {
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
}
</style>
