<template>
  <v-container fluid fill-height id="login">
    <div id="logo">
      <span v-for="char in 'myx'"
        :id="char"
        class="logo">
        {{ char }}
      </span>
    </div>

    <div id="google-signin"/>
  </v-container>
</template>

<script>
export default {
  mounted() {
    gapi.signin2.render('google-signin', {
      onsuccess: this.onSignIn,
    });

    for (let char of 'myx') {
      let elem = document.querySelector(`#${char}.logo`);
      elem.classList.add(`${char}Transition`);
    }
  },

  methods: {
    onSignIn(googleUser) {
      let profile = googleUser.getBasicProfile();
      let email = profile.getEmail();

      let bitArray = sjcl.hash.sha256.hash(email);
      let id = Math.abs(bitArray[0]) % 100000000;

      this.$store.dispatch('api/postCustomer', { id, email })
      .then(json => {
        this.$store.commit('login', { id, email });
        this.$store.dispatch('api/fetchAll');
        this.$router.push('/main');
      })
      .catch(console.error);
    },
  },
};
</script>

