  display: flex;
  flex-direction: row;
  justify-content: center;
}

.logo {
  font-size: $font-size-from;
}

.mTransition {
  animation-name: m;
  animation-duration: $duration;
  animation-fill-mode: forwards;
}

@keyframes m {
  33% { color: black; }
  66% { color: $primary-color; font-size: $font-size-from; }
  100% { color: $primary-color; font-size: $font-size-to; }
}

.yTransition {
  animation-name: y;
  animation-duration: $duration;
  animation-fill-mode: forwards;
}

@keyframes y {
  66% { font-size: $font-size-from; }
