}

.xTransition {
  animation-name: x;
  animation-duration: $duration;
  animation-fill-mode: forwards;
}

@keyframes x {
  0% { opacity: 0; }
  33% { opacity: 1; }
  66% { font-size: $font-size-from; }
  100% { font-size: $font-size-to; }
}
</style>
