<template>
  <v-container fluid fill-height class="pt-0 pb-2 px-1">
    <v-layout row wrap>
      <v-flex class="pa-2">
        <NearbyCard @click="handleNearby" />
        <v-divider/>
        <DiscoverCard @click="handleDiscover"/>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import { mapState } from 'vuex';
import NearbyCard from '@/components/main/NearbyCard.vue';
import DiscoverCard from '@/components/main/DiscoverCard.vue';

export default {
  components: {
    NearbyCard,
    DiscoverCard,
  },

  computed: mapState('api', ['stalls']),

  methods: {
    handleNearby() {
      this.$router.push('/nearby');
    },
    handleDiscover() {
      let target = 'myx';
      let stallUids = new Set(this.stalls.map(stall => stall.uid));
      if (stallUids.has(target)) this.$store.commit('setActiveStall', target);
      this.$router.push('/nearby');
    },
  },
};
</script>
